.plus_block {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 26px;
  height: 26px;
  border-radius: 13px;
  background-color: #000000;
  cursor: pointer;
  &:before {
    content: '';
    display: block;
    width: 14px;
    height: 2px;
    background-color: $text_color_1;
    transition: 0.5s all; }
  &:after {
    content: '';
    position: absolute;
    display: block;
    margin: 0 auto;
    width: 2px;
    height: 14px;
    background-color: $text_color_1;
    transition: 0.5s all; }
  &:hover {
    &:before, &:after {
      background-color: $text_color_2; } } }
